import { Router, Route } from "preact-router";

import { SiteHeader } from "@/components/SiteHeader";
import { Search, HomePage } from "@/pages";

export function App() {
  return (
    <>
      <SiteHeader />
      <Router>
        <Route path="/" component={HomePage} />
        <Route path="/search" component={Search} />
      </Router>
    </>
  );
}
